<script>
import * as api from "../../api/api-list";

export default {
  data() {
    return {
      messages: [],
      temp_messages: [],
      awardflaglist: [],
      awardtemp: [],
      blockflaglist: [],
      reverseblockflaglist: [],
      awardlist: [],
      cutlist: [],
      eventindex: 1,
      selectitem: [
        { text: "1차 이벤트", value: 1 },
        { text: "2차 이벤트", value: 2 },
        { text: "3차 이벤트", value: 3 },
        { text: "4차 이벤트", value: 4 },
        { text: "5차 이벤트", value: 5 },
      ],
      modalflag: true,
      modaltext: "",
    };
  },
  async created() {
    const res = await api.getmessages();
    this.messages = res.data.result;
    this.temp_messages = res.data.result;
    this.changeeventindex();
    this.setawardflag(this.messages);
    this.setblockflag(this.messages);
  },
  methods: {
    setawardflag(arr) {
      for (let i = 0; i < arr.length; i++) {
        this.awardflaglist.push(arr[i].isSelected);
      }
    },
    setblockflag(arr) {
      for (let i = 0; i < arr.length; i++) {
        this.blockflaglist.push(arr[i].block_status);
      }
    },
    awardmsg() {
      let amount = 0;
      for (let i = 0; i < this.temp_messages.length; i++) {
        if (this.temp_messages[i].isSelected) {
          amount += 1;
        }
        if (amount > 2) {
          console.log(amount);
          return;
        }
      }

      if (this.awardlist.length == 0) {
        console.log("empty");
        return;
      }

      api.awardmessage({ arr: this.awardlist });
      this.awardlist = [];
    },
    blockmsg() {
      if (this.cutlist.length == 0) {
        return;
      }
      api.cutmessage({ arr: this.cutlist });
      this.cutlist = [];
    },
    selectaward(data) {
      if (this.awardlist.includes(data)) {
        for (let i = 0; i < this.awardlist.length; i++) {
          if (this.awardlist[i] === data) {
            this.awardlist.splice(i, 1);
            i--;
          }
        }
      } else {
        this.awardlist.push(data);
      }
      console.log(this.awardlist);
    },
    selectblock(data) {
      if (this.cutlist.includes(data)) {
        for (let i = 0; i < this.cutlist.length; i++) {
          if (this.cutlist[i] === data) {
            this.cutlist.splice(i, 1);
            i--;
          }
        }
      } else {
        this.cutlist.push(data);
      }
      console.log(this.cutlist);
    },
    changeeventindex() {
      let arr = [];

      // if (this.eventindex == "0") {
      //   this.temp_messages = this.messages;
      //   return;
      // }

      for (let i = 0; i < this.messages.length; i++) {
        if (this.messages[i].index.toString() == this.eventindex.toString()) {
          arr.push(this.messages[i]);
        } else {
          continue;
        }
      }
      this.temp_messages = arr;
    },
  },
};
</script>

